
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: "FileLogin",
components: {
XHeader
},
data() {
return {
phoneNumber: "",
verifyCode: "",
disable: true,
isSend: false,
countdown: "发送验证码"
};
},
methods: {
sendCode() {
this.$ajax
.post(this.$root.host, {
"MSH.1": "user",
"MSH.2": "sendTelVerificationCode",
'tel': this.phoneNumber,
'type':"5"
})
.then(data => {
if (typeof data != "undefined" && typeof data.status != "undefined") {
if (data.status == 1) {
this.isSend = true;
this.timer();
} else if (data.status == 2) {
this.$vux.toast.text("发送失败", "bottom");
}
}
})
.catch(error => {
console.log(error);
});
},
timer() {
this.countdown = 60;
var timer = setInterval(() => {
this.countdown--;
if (this.countdown == 0) {
clearInterval(timer);
this.countdown = "发送验证码";
}
}, 1000);
},
NextStep() {
if (!this.disable) {
this.$ajax
.post("", {
"MSH.1": "user",
"MSH.2": "updateTel",
token: this.$root.loginInfo.token,
tel: this.phoneNumber,
password: this.$route.params.PWD,
telcode: this.verifyCode
})
.then(data => {
if (
typeof data != "undefined" &&
typeof data.status != "undefined"
) {
if (data.status == 1) {
this.$router.isBack = true;
this.$router.push('/mine');
let THIS = this;
this.$vux.toast.show({
text: "手机号更改成功，去登录",
type: "success",
onHide() {
if (THIS.$root.phoneType == "ios") {
window.webkit.messageHandlers.mutual.postMessage({
type: "1"
});
} else {
android: AndroidFun.mutual("1");
}
}
});
} else if (data.status == 2) {
this.$vux.toast.text("更改失败", "bottom");
}
}
})
.catch(error => {
console.log(error);
});
}
}
},
watch: {
phoneNumber(val) {
if (val.length == 11 && this.verifyCode.length >= 4) {
this.disable = false;
return;
}
this.disable = true;
},
verifyCode(val) {
if (val.length >= 4 && this.phoneNumber.length == 11) {
this.disable = false;
return;
}
this.disable = true;
}
}
};



